 la Constitución ocupa la cúspide de este orden, siendo la norma de normas que garantiza los derechos fundamentales y los principios básicos del Estado social de derecho. Por debajo de ella se sitúan las leyes, decretos y resoluciones, cuya validez está condicionada por su conformidad con la Constitución. Este conocimiento no solo es teórico; es esencial para evitar conflictos normativos y aplicar correctamente las normas, respetando el principio de supremacía constitucional.")])]),_c('div',{staticClass:"p-4",attrs:{"titulo":"Derecho internacional"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Aprenderá sobre la importancia del Derecho internacional en el contexto colombiano. En un mundo globalizado, los Estados no pueden ignorar los compromisos adquiridos a nivel internacional. Los tratados y convenios ratificados por Colombia, no solo son obligatorios en el plano internacional, sino que también, en muchos casos, se integran al bloque de constitucionalidad, adquiriendo una relevancia equivalente a la de las normas constitucionales. Conocer cómo se incorpora el Derecho internacional al orden interno, es vital para garantizar que las decisiones jurídicas se alineen con los estándares internacionales en materia de derechos humanos, desarrollo económico, medio ambiente y otras áreas de interés global.")])])])],1)]),_vm._m(2)])],1)}
